import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import Contact from './Contact'

const Navbar = () => {
  const [navbarState, setNavbarState] = useState(
    {
      container: 'nav_button_container',
      button: 'nav_button-open'
    }
  )

  const setNavbar = () => {
    if (navbarState.button === 'nav_button-open') {
      setNavbarState(
        {
          container: 'nav_button_container-showed',
          button: 'nav_button-open-cross'
        }
      )
    } else {
      setNavbarState(
        {
          container: 'nav_button_container-hidden',
          button: 'nav_button-open'
        }
      )
    }
  }

  return (
    <nav className='nav'>
      <img className='nav_logo' src='./images/brand/Logo + Texto (White).svg' />
      <button className={navbarState.button} onClick={setNavbar}>
        <svg id='menu_icon' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 120 69.5'>
          <title>Menu Icon</title>
          <g data-name='Capa 1'>
            <rect className='line-one' width='120' height='10' rx='5' />
            <rect className='line-two' y='30' width='120' height='10' rx='5' />
            <rect className='line-three' y='59.5' width='120' height='10' rx='5' />
          </g>
        </svg>
      </button>
      <div className={navbarState.container}>
        <Link className='nav_button' to='/'>Fotos</Link>
        <Link className='nav_button' to='/'>Fotos</Link>
        <Link className='nav_button' to='/'>Fotos</Link>
        <Link className='nav_button' to='/'>Fotos</Link>
        <Link className='nav_button-reservation' to='/reservations'>Haz tu Reserva</Link>
        <Contact />
      </div>
    </nav>
  )
}

export default Navbar
