import Oferta from './Oferta'
import Contact from './Contact'

const OfertaContainer = () => {
  return (
    <section className='section-precios'>
      <h2 className='caption'>Reserva tu Sesión</h2>
      <p className='text'>Contactame por mis redes sociales o por email para reservar una sesión ó para tener una oferta personalizada</p>
      <Contact />
      <div className='oferta_container'>
        <Oferta />
        <Oferta />
        <Oferta />
      </div>
    </section>
  )
}

export default OfertaContainer
